import { Paper, Stack, TextField, Typography } from '@mui/material'
import React from 'react'
import InputArea from './InputArea'
import { useDispatch } from 'react-redux'
import { useSelector } from 'react-redux'
import { setSupport } from '../../redux/supportSlice'
import Grid from '@mui/material/Grid2'


const Container = () => {
  // style
  const sizeStyle = {
    xs: 12, sm: 12, md: 6, lg: 6, xl: 4
  }

  return (
    <Paper elevation={3} sx={{  width: "50vw", backgroundColor: "rgba(255,255,255,0.95)" }}>
      <div style={{ padding: 35, paddingTop: 20 }}>
        <Typography variant='h4' sx={{ fontWeight: 600 }}></Typography>
        <Grid container spacing={0} rowSpacing={3} sx={{ mt: 1 }}>
          <Grid size={sizeStyle}><InputArea text="" action="baseParams"></InputArea></Grid>
          <Grid size={sizeStyle}><InputArea text="(%)" action="lessonBonus"></InputArea></Grid>
          <Grid size={sizeStyle}><InputArea text="" action="limitIncrease"></InputArea></Grid>
          <Grid size={sizeStyle}><InputArea text="" action="lesson"></InputArea></Grid>
          <Grid size={sizeStyle}><InputArea text="SP" action="spLesson"></InputArea></Grid>
          <Grid size={sizeStyle}><InputArea text="" action="selectRest"></InputArea></Grid>
          <Grid size={sizeStyle}><InputArea text="" action="selectProvide"></InputArea></Grid>
          <Grid size={sizeStyle}><InputArea text="" action="selectGoOut"></InputArea></Grid>
          <Grid size={sizeStyle}><InputArea text="" action="selectSoudan"></InputArea></Grid>
          <Grid size={sizeStyle}><InputArea text="" action="selectClass"></InputArea></Grid>
          <Grid size={sizeStyle}><InputArea text="" action="getDrink"></InputArea></Grid>
          <Grid size={sizeStyle}><InputArea text="(1)" action="endTest"></InputArea></Grid>
        </Grid>
        <Typography variant='h4' sx={{ fontWeight: 600, mt: 4 }}></Typography>
        <Grid container spacing={0} rowSpacing={3} sx={{ mt: 1 }}>
          <Grid size={sizeStyle}><InputArea text="" action="getSkill"></InputArea></Grid>
          <Grid size={sizeStyle}><InputArea text="M" action="getMSkill"></InputArea></Grid>
          <Grid size={sizeStyle}><InputArea text="A" action="getASkill"></InputArea></Grid>
          <Grid size={sizeStyle}><InputArea text="" action="skillUpgrade"></InputArea></Grid>
          <Grid size={sizeStyle}><InputArea text="M" action="mSkillUpgrade"></InputArea></Grid>
        </Grid>
      </div>
    </Paper>
  )
}

export default Container